body {
  background: #FFF;
  padding: 20px;
  font-family: Helvetica;
}

.slider {
  color: #333;
  padding:0 0 30px;
}

.slide {
  font-size: 90px;
  width: 150px;
  padding:10px 0;
  margin:20px 15px;
  text-align: center;
  border: 1px solid #999;
}
.slide.locked { 
	background: rgba(255,255,0,0.5); 
	position: fixed;
	width: 150px;
	height: 112px;
}

.slick-prev, .slick-next {
  position: absolute;
  bottom:0;
}
.slick-next { left:80px; }

.slick-disabled {
	pointer-events: none;
	cursor: default;
	opacity: 0.5;
}

.slick-slider .slick-track, .slick-slider .slick-list {
	transform: none;
}